body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.large-view-layout .list-items {
  position: fixed;
  height: 100%;
  width: 320px;
  top: 0px;
  bottom: 0px;
  background-color: #333;
  color: #FFF;
  overflow-y: auto;
  z-index: 999; }
  .large-view-layout .list-items .list-item {
    padding: 15px 10px 15px 15px;
    cursor: pointer;
    -webkit-transition: background-color 0.32s ease;
    transition: background-color 0.32s ease;
    position: relative;
    box-sizing: border-box;
    border-left: solid 3px transparent;
    max-width: 100%; }
    .large-view-layout .list-items .list-item button.copy-item {
      font-size: 10px;
      padding: 8px; }
    .large-view-layout .list-items .list-item .delete-btn {
      color: #FFF;
      background-color: transparent;
      margin-left: 5px; }
    .large-view-layout .list-items .list-item:hover {
      background-color: #555; }
  .large-view-layout .list-items .selected {
    border-left-color: red; }

.large-view-layout .copy-btn {
  padding: 20px; }

.large-view-layout .content {
  margin-left: 320px;
  position: relative;
  z-index: 2; }

.top-controls {
  box-shadow: 0px 5px 26px -6px #DDD;
  padding: 12px;
  display: flex;
  justify-content: space-between; }

.top-controls input[type="file"] {
  display: none; }

.top-controls .file-import {
  display: inline-block;
  border: dashed 2px #DDD;
  padding: 12px;
  cursor: pointer;
  border-radius: 3px;
  -webkit-transition: border-color 0.24s ease;
  transition: border-color 0.24s ease;
  margin-right: 5px; }
  .top-controls .file-import:hover {
    border-color: #888; }

.top-controls .delete-all-btn {
  background-color: transparent;
  padding: 8px 10px;
  color: #111; }

.top-controls .side-controls {
  display: flex;
  justify-content: center;
  flex-flow: row nowrap; }
  .top-controls .side-controls.right {
    margin-right: 0px;
    margin-left: auto; }

.quest-details-form {
  background-color: #FFF;
  padding: 15px;
  position: relative;
  text-align: left;
  margin: 10px auto;
  display: block; }
  .quest-details-form.multi {
    box-shadow: -3px 4px 18px -5px #BBB;
    border-radius: 3px; }

.quest-details-form .top-controls {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.quest-details-form .add-line {
  display: inline-block;
  border: none;
  background-color: #553f2b;
  color: #FFF;
  font-weight: bolder;
  padding: 10px 24px;
  border-radius: 4px; }

.quest-details-form .line-counter {
  font-size: 25px; }

.quest-details-form .details-input-boxes {
  padding: 12px 0px; }

.quest-details-form .plain-text-wrapper {
  border-top: dashed 3px #DDD;
  padding: 15px 0px; }

.quest-details-form .top {
  display: block;
  padding-bottom: 12px;
  margin-bottom: 12px;
  border-bottom: solid 2px palevioletred; }

.quest-details-form code {
  margin-bottom: 10px; }

.quest-details-form .copy-btn {
  display: block;
  margin: auto;
  width: 85%;
  max-width: 480px;
  font-size: 18px;
  background-color: #6950f7; }

.quest-details-form .bottom-controls {
  margin-top: 5px; }
  .quest-details-form .bottom-controls .add-line-below {
    background-color: transparent;
    background-color: purple;
    padding: 5px 10px;
    margin-left: 5px;
    margin-right: 5px; }

.quest-details-form .load-new-details {
  margin-top: 10px;
  background-color: #df5506; }

.quest-details-form .delete-btn {
  background-color: transparent;
  color: #111;
  font-size: 12px;
  padding: 4px 12px; }

.quest-details-form .bottom-details {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.quest-details-form .load-quest-details p {
  margin-bottom: 8px; }

.quest-details-form label {
  display: block;
  margin: 5px auto 15px; }
  .quest-details-form label input[type="text"] {
    font-size: 18px;
    border-radius: 5px;
    border: solid thin #DDD;
    padding: 12px 8px;
    display: block;
    width: 100%;
    box-sizing: border-box; }
  .quest-details-form label .bottom-controls {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
  .quest-details-form label .character-counter {
    display: inline-block;
    font-weight: bolder;
    font-size: 12px;
    text-align: left;
    padding: 5px 0px; }
  .quest-details-form label .delete-btn {
    display: inline-block;
    margin: 2.5px 2.5px;
    font-size: 18px;
    cursor: pointer; }

.quest-form > label {
  padding: 20px 15px;
  display: block; }
  .quest-form > label .field-name {
    font-size: 14px;
    font-weight: bolder;
    margin-bottom: 6px; }
  .quest-form > label input[type="text"] {
    border: solid thin #DDD;
    padding: 12px;
    font-size: 20px;
    display: block;
    box-sizing: border-box;
    border-radius: 4px;
    width: 100%; }

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  background-color: #FEFEFE;
  position: relative;
  overflow-x: hidden; }

.app {
  text-align: center;
  padding: 25px; }

button, input[type="submit"] {
  cursor: pointer;
  padding: 12px;
  background-color: #111;
  color: #FFF;
  border: none;
  font-weight: bolder;
  border-radius: 4px; }

code {
  display: block;
  white-space: nowrap;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  overflow-x: auto;
  padding: 20px; }

.quest-details-form-wrapper {
  display: inline-block;
  width: 480px;
  margin: 12px;
  vertical-align: top; }
  @media all and (max-width: 900px) {
    .quest-details-form-wrapper {
      display: block;
      width: 95%;
      box-sizing: border-box;
      margin: auto; } }

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center; }

